@import "src/common/styles/sass/mixins";
@import "src/common/styles/sass/variables";
@import "src/common/common components/Button/Button.module";

.contactsBlock {
    min-height: 50vh;
    background-color: $bgPrimary;
    .contactsContainer {
        @include container();
        justify-content: center;
        form {
            margin-bottom: 170px;
            width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            @media screen and (max-width: 500px) {
                width: 90%;
            }
            .fields {
                width: 100%;
                display: flex;
                flex-direction: column;
                input, textarea {
                    margin: 10px;
                    background: $bgSecondary !important;
                    border: 1px solid #4a4a4a;
                    color: $textColor;
                    &:-webkit-autofill {
                        border: 1px solid #4a4a4a;
                        -webkit-text-fill-color: $bgSecondary;
                        -webkit-box-shadow: 0 0 0 1000px transparent inset;
                        transition: background-color 5000s ease-in-out 0s;
                        background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(0,174,255,0.04) 50%,rgba(255,255,255,0) 51%,rgba(0,174,255,0.03) 100%);
                        &:focus{
                            border: 2px solid $bgSecondary;
                        }
                    }
                }
                textarea {
                    margin-top: 0;
                    margin-bottom: 0;
                    height: 82px;
                }
                span {
                    color: $textColor;
                }
            }
            input[type="submit"] {
                display: none;
            }
            a {
                margin-top: 15px;
                @extend .btn;
                cursor: pointer;
            }
        }
    }
}

