@import "src/common/styles/sass/variables";

.headerBlock {
    height: 60px;
    width: 100%;
    background-color: $bgSecondary;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: fixed;
    z-index: 10;
    opacity: .7;
    @media screen and (max-width: 1024px){
        justify-content: center;
    }
}